.cases {
	height: 50vh;
	height: calc(var(--vh, 1vh) * 50);

	&-navigation {
		position: absolute;
		bottom: 0;
		width: 100%;
		display: flex;
		justify-content: space-between;
		align-items: center;
		z-index: 6;
		padding: 32px;
		box-sizing: border-box;

		@include media('<=tablet') {
			display: none;
		}
	}
	&-arrow {
		background-color: rgba($black, 0.4);
		height: 72px;
		width: 72px;
		border-radius: 50%;
		display: flex;
		align-items: center;
		justify-content: center;

		svg {
			color: $white;
			height: 28px;
			width: 28px;
		}
		&.disabled {
			opacity: 0.3;
		}
	}
	.row {
		@include media('<=tablet') {
			flex-direction: column;
		}

		.case {
			position: relative;
			background-color: $black;
			cursor: pointer;
			&:hover {
				.case-image {
					opacity: 0.4;
				}
			}

			&-details {
				width: calc(100vw / 3);
				height: 50vh;
				height: calc(var(--vh, 1vh) * 50);
				display: flex;
				flex-direction: column;
				justify-content: center;
				padding: 32px;
				box-sizing: border-box;
				z-index: 1;
				position: relative;

				@include media('<=tablet') {
					width: 100vw;
				}

				@include media('<=phone') {
					padding: 16px;
				}
				span {
					margin-top: 156px;
					font-size: 1.6rem;
					opacity: 0.8;
					color: $white;
					font-weight: 600;

					@include media('<=desktop', '>tablet') {
						font-size: 1.4rem;
					}

					@include media('<=phone') {
						font-size: 2.4rem;
						line-height: 2.2rem;
					}
				}
				h2 {
					font-size: 2.4rem;
					line-height: 3.4rem;
					width: 85%;
					margin-top: 16px;
					color: $white;

					@include media('<=desktop', '>tablet') {
						font-size: 2rem;
						line-height: 2.4rem;
					}

					@include media('<=phone') {
						font-size: 1.7rem;
						line-height: 2.2rem;
						margin-top: 8px;
					}
				}
			}
			&-image {
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				overflow: hidden;
				position: absolute;
				opacity: 0.65;
				transition: 0.4s cubic-bezier(0.6, -0.05, 0.1, 0.99);

				img {
					height: 100%;
					width: 100%;
					object-fit: cover;
				}
			}
		}
	}
}
